import React from 'react';
import { FormGroup, Label, Input, Button } from 'reactstrap';

export default function NavigateSection({ mapData }) {
    const selectOptionStyle = {
        fontSize: 10,
    };

    return (
        <div
            style={{
                fontSize: 10,
            }}
        >
            <div>
                <Label
                    for="navigate-origin"
                    className="font-weight-bold mb-1 mt-2"
                >
                    Origin
                </Label>
                <Input
                    style={selectOptionStyle}
                    type="select"
                    name="select"
                    id="navigate-origin"
                >
                    {Object.values(mapData.locations).map((location) => {
                        return (
                            <option style={selectOptionStyle}>
                                {location.id}
                            </option>
                        );
                    })}
                </Input>
            </div>
            <div>
                <Label
                    for="navigate-dest"
                    className="font-weight-bold mb-1 mt-2"
                >
                    Destination
                </Label>
                <Input
                    style={selectOptionStyle}
                    type="select"
                    name="select"
                    id="navigate-dest"
                >
                    {Object.values(mapData.locations).map((location) => {
                        return (
                            <option style={selectOptionStyle}>
                                {location.id}
                            </option>
                        );
                    })}
                </Input>
            </div>
            <Button
                className="mt-2"
                color="primary"
                style={{
                    fontSize: 10,
                }}
            >
                Navigate
            </Button>
        </div>
    );
}
